import Article from "@/components/article/Article.vue";

export default {
    name: 'Home',
    components: {
        Article
    },
    props: {
        scrolled: Number,
        bgHeight: Number
    },
    computed: {
        posts: function () {
            return this.$store.state.home.postsList.data;
        },
        pagination() {
            const { pageCount, pageSize, total } = this.$store.state.home.postsList;
            return { pageCount, pageSize, total };
        },
        page() {
            return this.$store.state.home.page;
        },
        format() {
            return this.$store.state.global.hexoConfig.dateTimeFormat.date_format;
        },
        shouldPage() {
            return this.$store.getters[`global/shouldPagination`];
        }
    },
    async fetch({ store }) {
        const prePage = store.state.home.page;
        // avoid double fetch initial data
        if (prePage !== 1) {
            await store.dispatch(`home/fetchHomePostsList`, { page: 1 });
        }
    }
}